/* Base styles */
.alert {
  position: relative;
  padding: 15px 20px;
  border-radius: 2px;
}

/* Provide class for links that match alerts */
.alert-link {
  font-weight: 600;
  transition: all 0.3s ease-in-out;
}

.alert-link:hover {
  text-decoration: underline;
}

.alert-dismissible {
  padding-right: 40px;
}

.alert-dismissible .close {
  padding: 14px 10px;
}

/* Alternate styles
 Generate contextual modifier classes for colorizing the alert. */
.alert-primary {
  color: #2c70a4;
  border-color: #c1e2fc;
}

.alert-primary hr {
  border-top-color: #a9d7fb;
