/**====== Alert css end ======**/
.nav {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.nav-link {
  display: block;
  padding: 10px 24px;
}

.nav-link:hover {
  text-decoration: none;
}

.nav-link.disabled {
  opacity: 0.7;
}

/* ======================== Tabs =============================== */
.nav-tabs {
  border-bottom: none;
}

.nav-tabs .nav-link {
  border: none;
  color: #686c71;
}

.nav-tabs .nav-link:hover {
  color: #2196f3;
}

.nav-tabs .nav-link.disabled {
  opacity: 0.7;
}

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  border: none;
  color: #111;
  box-shadow: 0px -1px 0 1px #e2e5e8;
}

/* =======================   Pills ================== */
.nav-pills {
  padding: 15px;
  background-color: #fff;
}

.nav-pills .nav-link {
  border-radius: 2px;
}

.nav-pills .nav-link.active,
.nav-pills .show>.nav-link {
  color: #fff;
  background: #2196f3;
}

/* ==================== Justified variants ===================*/
.nav-fill .nav-item {
  flex: 1 1 auto;
  text-align: center;
}

.nav-justified .nav-item {
  flex-basis: 0;
  flex-grow: 1;
  text-align: center;
}

/* Tabbable tabs */
/* Hide tabbable panes to start, show them when .active */
.tab-content {
  padding: 35px 30px;
  background: #fff;
  box-shadow: 0 0 0 1px #e2e5e8;
}

.tab-content>.tab-pane {
  display: none;
}

.tab-content>.active {
  display: block;
}


